@use "global" as *;

.btn{
  display: inline-block;
  width: 100%;
  max-width: rem(335);
  padding: rem(10) 0;
  text-align: center;
  font-size: rem(20);
  letter-spacing: rem(2);
  border: 1px solid $white;
  background: $text-color;
  color: $white;
  cursor: pointer;
  @include mq(md){
    position: relative;
    overflow: hidden;
    transition: ease .3s;
  }
}
.btn::before {
  content: '';
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transform: translateX(-100%);
  transition: 0.3s ease-in-out;
  z-index: -1;
}
.btn:hover::before{
  transform: translateX(0);
}
